import axios from "axios"

export default {
  methods: {
    // post 호출
    async sendPost(url, data = {}) {
      // 토큰 정보 없을 경우 조회
      if (!this.$store.state.authToken) {
        await this.callGetAuthToken();
      }
      // 요청 설정
      let config = {};
      // 헤더 설정 (인증 토큰)
      config.headers = {
        Authorization: `Bearer ${this.$store.state.authToken}`
      }
      config.httpAgent = {
        rejectUnauthorized: false
      }
      // API 호출
      return (await axios.post(url, data, config)).data
    },
    // Native App 토큰 설정
    async callGetAuthToken() {
      for (let i = 0; i < 3; i++) {
        try {
          console.log("=== callGetAuthToken === " + i);
          this.getAuthToken();

          if (!this.$store.state.authToken) {
            await this.sleep(500);
          } else {
            break;
          }
        } catch (error) {
          console.log(error);
        }
      }
    },
    // Native App 연동 - 토큰 조회
    getAuthToken() {
      if (this.isAndroid()) {
        window.android.getAuthToken();
      } else if (this.isIOS()) {
        let message = {
          action: "getAuthToken",
        };
        window.webkit.messageHandlers.experCounselHandler.postMessage(message);
      }
    },
    // Native App 연동 - 토큰 받기
    responseGetAuthToken(token) {
      console.log(token);
      this.$store.state.authToken = token;
    },
    // android check
    isAndroid() {
      if (navigator.userAgent.match(/Android/i)) {
        return true;
      } else {
        return false;
      }
    },
    // ios check
    isIOS() {
      if (navigator.userAgent.match(/iPhone|iPad/i)) {
        return true;
      } else {
        return false;
      }
    },
    // 시간 지연
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
  },
};
