import { createWebHistory, createRouter } from "vue-router"

const routes = [
  {
    path: "/",
    redirect: "/kms",
  },
  {
    path: "/kms",
    component: () => import("@/components/AwsKms.vue"),
  }
];

const router = createRouter({ 
    // history: createWebHistory(process.env.NODE_ENV === 'development' ? '/' : '/appkms'),
    history: createWebHistory(),
    routes 
});

export default router;
